.app-wrapper {
    display: grid;
    margin: 0 auto;
    width: 1200px;


    grid-template-areas:
    "h h h h"
    "n c c c"
    ;
    grid-template-rows: 60px 1fr;
    grid-template-columns:  150px 10fr;
}

.app-wrapper-content{
    grid-area: c;
    background-color: grey;
}

.active-link{
    color: gold;
}


